// if screen width less than 1050px
@media screen and (max-width: 1050px) {
  .payment {
    &-form {
      width: 100% !important;
      margin: 0 !important;
    }
  }
}
// if screen width less than 660px
@media screen and (max-width: 660px) {
  .payment {
    flex-direction: column;
    width: 95%;
    margin: auto;

    &-sidebar,
    &-form {
      width: 100% !important;
      h3:last-of-type {
        margin: 1em 0 -0.5em 0 !important;
      }

      &-card {
        
        i {
          font-size: 11vw !important;
        }
      }
    }
  }
}
// styles for payment section
.payment {
  display: flex;
  flex-grow: 1;

  input[type="number"] {
    appearance: textfield;
  }

  &-sidebar {
    width: 15em;
    background-color: $_dark-grey;
    color: $_highlight;

    > :nth-child(n + 2) {
      margin: 0.5em 1em;
    }

    &-cost {
      > :nth-child(n + 2) {
        margin-left: 0.5em;
        display: flex;
        > :first-child {
          flex-grow: 1;
        }
      }

      > :last-child {
        margin-left: 0;
      }
    }

    img {
      width: 100%;
    }

    > h4 {
      margin: 1em 1em 1em 1.5em !important;
      input {
        width: 3em;
        float: right;
      }
    }
  }

  &-form {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    margin: 1em auto;
    background-color: $_light-grey;
    padding: 1em;
    border-radius: 4px;
    height: min-content;

    > h3:last-of-type {
      margin: 2em 0 -1em 0;
    }

    h4 {
      align-self: flex-end;
    }

    &-card {
      > div {
        white-space: nowrap;
      }
    }

    &-shipping,
    &-card {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      > :nth-child(n) {
        display: block;
        width: 49%;
        margin: 0.25em 0 0.25em 0;
      }
    }

    > button {
      margin-top: 1em;
      width: min-content;
      padding: 0.5em;
      white-space: nowrap;
      border: 0;
      background-color: $_primary;
      color: $_highlight;
      font-weight: 700;
      font-size: 1em;
      align-self: flex-end;
    }
  }
}
