// if screen width is less that 920px
@media screen and (max-width: 920px) {
  #location {
    flex-direction: column;
    align-items: center;

    &-map {
      width: 90% !important;
      height: 50vw !important;
    }

    &-list {
      width: 90% !important;
    }
  }
}
// if aspect ratio is less that 1361 by 964 (if viewport width is less than x of height)
@media (max-aspect-ratio: 1361/964) {
  #home-showcase-img img {
    width: auto !important;
    height: 100% !important;
  }
}
// if aspect ratio is less that 1 by 1 (if viewport is in portrait aspect ratio)
@media (max-aspect-ratio: 1/1) {
  .img-carousel > div h2 {
    display: block !important;
    background-color: rgba(0, 0, 0, 0.6);
    padding-bottom: 1em;
  }
}
// style for landing img section of site
#landing {
  width: 100%;
  height: 92vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  #home-showcase-img {
    height: 70vh;
    align-self: center;
    overflow: hidden;
    display: flex;

    img {
      object-fit: cover;
      width: 100%;
      min-width: 100%;
    }
  }

  .img-carousel {
    height: 30vh;
    overflow-x: auto;
    overflow-y: hidden;
    display: flex;
    scrollbar-width: thin;
    scrollbar-color: #4d4c4c80 #bbbbbb8c;

    > div {
      height: 100%;
      margin: 0.25em 0.125em;
      cursor: pointer;

      img {
        height: 100%;
        min-width: 100%;
      }

      h2 {
        display: none;
        margin-top: -2em;
        text-align: center;
        color: white;
        position: relative;
        z-index: 10;
      }

      &:hover img {
        filter: blur(5px) grayscale(100%) brightness(50%);
        transition: 1s;
      }

      &:hover h2 {
        display: block !important;
      }
    }
  }
}
// styling for store locator section of site
#location {
  display: flex;
  border-top: 0.1em grey solid;

  &-map {
    width: 75%;
    height: 25vw;
    margin: 1em;

    iframe {
      width: 100%;
      height: 100%;
    }
  }
  &-list {
    width: 25%;
    margin: 1em;
    display: flex;
    flex-direction: column;

    & button {
      width: 100%;
      height: 3em;
      text-align: left;
      font-weight: 700;
      background-color: #c0c0c0;
      border: none;
      margin: 0.25em 0;
      cursor: pointer;
    }

    & button:hover,
    & .active {
      background-color: $_primary;
      color: white;
      transition: 0.5s;
    }
  }
}
