// Main CSS
* {  
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-width: 300px;

  input, textarea, button {
    font-family: inherit;
  }

  > div:nth-last-child(3){
    flex-grow: 1;
  }
}

.hide {
  display: none !important;
}

