// Mobile Override
@media screen and (min-width: 1050px) {
  header {
    .hide {
      display: flex !important;
    }
  }
}
@media screen and (max-width: 1050px) {
  header {
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    .header-elements {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 95%;

      &-top {
        flex-direction: column !important;
        position: absolute;
        top: 75px;
        right: 0px;
        background-color: $_primary;

        button {
          width: 130px;
          padding-left: 0.75em !important;
          text-align: left !important;
        }
      }
    }

    .mobile-menu {
      display: block !important;
    }
  }
}

// Desktop
header {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  min-height: 75px;
  background-color: $_primary;

  img {
    padding: 0em 1em 0em 1em;
    width: 250px;
    height: 25px;
  }

  div {
    flex-grow: 1;
    display: flex;
  }

  .header-elements {
    color: $_highlight;
    flex-grow: 1;
    margin-right: 1em;
    flex-direction: column;
    // Top Buttons

    &-top {
      display: flex;
      align-items: center;
      height: 50%;

      & button,
      div {
        height: 100%;
        font-size: 1em;
      }

      & button {
        border: none;
        text-decoration: none;
        font-weight: bolder;
        background-color: transparent;
        color: $_highlight;
        cursor: pointer;

        &:hover {
          background-color: $_secondary;
        }

        &:last-of-type {
          display: flex;
          align-items: center;

          i {
            margin-right: 0.25em;
            font-size: 1.5em;
          }
        }
      }

      & div {
        flex-grow: 1;
      }
    }

    // Seach Bar
    &-search {
      height: 50%;
      padding: 0;
      display: flex;
      align-items: center;
      width: 100%;

      i {
        padding: 5px;
        color: $_highlight;
      }

      input {
        border: none;
        border-bottom: 1px solid $_highlight;
        background-color: transparent;
        flex-grow: 1;

        &:focus {
          outline: none;
          color: $_highlight;
          font-weight: bold;
        }
      }
    }
  }

  //Mobile Menu
  .mobile-menu {
    display: none;
    margin: 0 0.1em 0 0;
    color: $_highlight;
  }
}
