// if screen width less than 1050px
@media screen and (max-width: 1050px) {
  .product {
    width: 80% !important;
    
    &-showcase {
      height: 14.5vh !important;
    }
  }
}
// if screen width is less than 700px
@media screen and (max-width: 700px) {
  .product {
    width: 100% !important;

    &-blurb {
      flex-direction: column;
      align-items: center;

      &-img {
        width: 80% !important;
      }

      &-text {
        width: 80% !important;
      }
    }

    &-showcase {
      width: 100%;
    }

    &-info {
      width: 80%;
      align-self: center;
      
      div {
        justify-content: space-between;
        flex-wrap: wrap;
      }
    }
  }
}
// styles for product section
.product {
  margin: 1em auto;
  display: flex;
  flex-direction: column;
  width: 70%;
  max-width: 1725px;

  &-blurb {
    display: flex;

    &-img {
        min-width: 30%;
        max-width: 425px;

      img {
        width: 100%;
      }

      div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        white-space: nowrap;

        h3 {
          font-weight: 500;
          margin: 0 auto;
        }

        button {
          padding: 0.5em 1rem;
          border: none;
          background-color: $_primary;
          color: $_highlight;
          cursor: pointer;
        }
      }
    }

    &-text {
      min-width: 50%;
      padding: 1em;

      h2 {
        margin-top: 0;
      }

      p {
        margin-bottom: 0;
      }
    }
  }

  &-showcase {
    margin-top: 1em;
    height: 25vh;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    scrollbar-width: thin;
    scrollbar-color: #4d4c4c80 #bbbbbb8c;

    img {
      height: 95%;
      margin: 0.25em;
    }
  }
  &-info {
      padding: 0 1em;

      h3 {
          margin: 1em 0 0 0;
      }

      div {
          display: flex;
          justify-content: space-between;
      }
  }
}
