// if screen width is less that 920px
@media screen and (max-width: 600px){
    .login > div {
        flex-direction: column;
        min-width: 95%;
        justify-content: center;

        > :nth-child(n) {
            width: 100%;
        }

        :last-child {
            border: 0 !important;
        }
    }
}
// style for login section of site
.login {
  display: flex;
  align-items: center;

  > div {
    display: flex;
    align-items: flex-start;
    margin: auto;
    background-color: #eeeeee;
    padding: 1em;
    border-radius: 4px;

    > div:nth-child(n) {
      display: flex;
      flex-direction: column;

      :nth-child(n) {
        margin: 0.5em 1em;
      }
    }

    :last-child {
        border-left: 1px $_dark-grey solid;
    }

    label {
        font-weight: 500;
    }

    button {
      margin-top: 1em;
      width: min-content;
      padding: 0.5em;
      white-space: nowrap;
      border: 0;
      background-color: #eb2340;
      color: #ffffff;
      font-weight: 700;
      font-size: 1em;
      align-self: flex-end;
    }
  }
}
