@media screen and (max-width: 920px) {
  .footer {
    flex-direction: column-reverse !important;

    &-index {
      width: 100% !important;
    }

    &-contact {
      width: 100% !important;

      &-elms > div {
        margin: 0 auto !important;
      }
    }
  }
}

.footer {
  background-color: $_dark;
  color: white;
  display: flex;
  width: 100%;
  justify-content: space-evenly;

  &-index {
    width: 50%;
    display: flex;
    margin: 1em 0 3em 0;

    div {
      margin: 0 auto;

      &:last-of-type {
        padding-top: 2.7em;
      }
    }
  }

  &-break {
    margin: 1em 2em;
  }

  &-contact {
    width: 50%;
    margin-top: 1em;

    &-elms {
      display: flex;
      align-items: center;
      height: 70%;
      flex-wrap: wrap;

      & > div:first-of-type {
        margin: 0 1em 0 0;
        padding-left: 0.5em;

        i {
          margin-right: 0.5em;
        }
      }

      & > div:last-of-type {
        padding-right: 0.5em;
      }

      &-email {
        display: flex;
        align-items: center;

        input {
          height: 2em;
          margin-right: 0.25em;
        }
      }

      &-social {
        padding-top: 3em;
      }
    }
  }

  a,
  .fa-envelope-square,
  .fa-facebook-square,
  .fa-google-plus-square,
  .fa-instagram-square {
    cursor: pointer;
  }
}
