// if screen width less than 1050px
@media screen and (max-width: 1050px) {
  .category {
    max-width: 100% !important;

    &-category {
      overflow-x: auto !important;
      margin: 0.5em 0em !important;
      padding: 0 5em 0.5em 1em !important;
      border: none !important;

      &-heading {
        border: 1px solid grey;
        border-radius: 4px 0 0 4px;
        border-right: none !important;
      }
      &-content {
        overflow-x: visible !important;
        border: 1px solid grey;
        border-radius: 0 4px 4px 0;

        & div:last-of-type {
          padding-right: 0 !important;
        }
      }
      & hr {
        display: block !important;
        color: transparent;
        min-width: 1em;
      }
    }
  }
}
// styling for category section
.category {
  min-height: 70vh;
  max-width: 80%;
  margin: 1em auto;
  display: flex;
  flex-direction: column;
  justify-content: center;

  &-category {
    display: flex;
    border: 1px solid grey;
    border-radius: 4px;
    margin: 0.5em;
    overflow: hidden;
    scrollbar-width: thin;
    scrollbar-color: $_dark-grey $_grey;

    &:first-of-type {
      margin-top: 1em;
    }
    &:last-of-type {
      margin-bottom: 1em;
    }

    &-heading {
      border-right: 1px solid grey;
      cursor: pointer;

      img {
        height: 10em;
      }
      p {
        text-align: center;
        margin-top: 0.15em;
      }
    }

    &-content {
      display: flex;
      overflow-x: auto;
      scrollbar-width: thin;

      div {
        margin: 0 0.5em;
        cursor: pointer;

        &:first-of-type {
          margin-left: 1em;
        }
        &:last-of-type {
          padding-right: 1em;
        }

        img {
          height: 10em;
        }

        p {
          text-align: center;
          margin: 0.15em 0;
        }
      }
    }

    & hr {
      display: none;
    }
  }
}
